import React, { Component } from 'react';
import Lists from './components/Lists';
import Navbar from './components/Navbar';
import NewListForm from './components/NewListForm';
import { fetchLists } from './actions/listActions';
import {  } from './actions/listActions';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
class App extends Component {

  constructor(props){
    super(props);
    this.onFetchLists = this.onFetchLists.bind(this);
    this.state = { hasLoadedOnce: false }
  }

  onFetchLists(){
    this.props.fetchLists('/listr_api/lists/', this.props.token);
  }

  componentWillMount(){

  }

  render() {
    var data;
    var addButtonStyle = {
      display: 'none'
    };
    if(this.props.isLoading){
      data = <p>Loading Data</p>
    }else if(this.props.error){
      data = <p>{this.props.error}</p>
    }
    if(this.props.token && this.state.hasLoadedOnce === false){
      this.props.fetchLists('/listr_api/lists/', this.props.token);
      this.setState({hasLoadedOnce: true});

    }
    if(!this.props.token){

    }
    if(this.props.lists && this.props.token){
      data = <Lists data={this.props.lists} />
        addButtonStyle = {
          marginLeft: '10px',
          marginTop: '-15px'
        };
    }else{
      data = <p>No Lists to Show</p>
    }
    const headerStyle = {
      display: 'inline'
    };

    return (
      <div>
        <Navbar />
        <div class="container">
          <header class="App-header">
            <h1 class="App-title" style={headerStyle}>My Lists</h1>
            <button class="btn btn-outline-dark" data-toggle="modal" data-target="#newListModal" style={addButtonStyle}><i class="fa fa-plus"></i></button>
          </header>
          <button class='btn btn-primary' onClick={this.onFetchLists}><i class="fa fa-sync-alt"></i></button>
          {data}
          <NewListForm />
        </div>
      </div>
    );
  }
}

App.proptypes = {
  fetchLists: PropTypes.func.isRequired,
  lists: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  listUrl: state.listUrl,
  token: state.users.token,
  lists: state.items.data,
  error: state.items.error,
  isLoading: state.items.loading
});

export default connect(mapStateToProps, { fetchLists })(App);



