import { FETCH_LISTS, FETCH_LISTS_SUCCESS } from "../actions/types";

export default function listReducer(state = {data: [], loading: true}, action){
    switch (action.type) {
      case FETCH_LISTS:
        return {
          ...state,
          loading: true
        }
      case FETCH_LISTS_SUCCESS:
        if(action.payload.detail){
          return{
            loading: false,
            error: action.payload.detail
          }
        }else{
          return {
            data: action.payload,
            loading: false
          }
        }
      default:
        return state
    }
}



