import { FETCH_LISTS, FETCH_LISTS_SUCCESS, LIST_POST_SUCCESS, LIST_DELETE_SUCCESS } from './types';
import Cookies from 'js-cookie';

export const beginFetchLists = () => dispatch =>{
  return{
    type: FETCH_LISTS
  }
}

export const fetchLists = (url, token) => dispatch =>{
    dispatch(beginFetchLists());
    fetch(url, {
      method: 'GET',
      headers: {
        'content-type': 'application/json',
        'Authorization': 'Token ' + token
      },
    })
    .then(res => res.json())
    .then(data => dispatch({
      type: FETCH_LISTS_SUCCESS,
      payload: data
    }));
}

export const deleteList = (url, listId, token) => dispatch => {
  var csrftoken = Cookies.get('csrftoken');
  fetch(url+listId+'/', {
    method: 'DELETE',
    headers: {
      'content-type': 'application/json',
      'Authorization': 'Token ' + token
    },
  })
  .then(res => {
    if(res.status === 204){
      dispatch(fetchLists(url, token));
    }else{
      alert(res.json().detail);
    }
  });

}

export const performListPost = (url, listData, token) => dispatch => {
  var csrftoken = Cookies.get('csrftoken');
  fetch(url, {
    method: 'POST',
    headers: {
      'content-type': 'application/json',
      'Authorization': 'Token ' + token,
      'X-CSRFToken': csrftoken
    },
    body: JSON.stringify(listData)
  })
  .then(res => {
    if(res.status === 201){
      dispatch(fetchLists(url, token));
    }else{
      alert(res.json());
    }
  })


}



